/* Hero Headline minimal styles (extend as needed) */
.block-type--hero_headline.block-view-mode--compact .content .text {
  font-size: 1.445em;
}

.block-type--hero_headline.block-view-mode--default {
  border: 1px solid purple;
  /* nested */
}
.block-type--hero_headline.block-view-mode--default .block-wrap {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 12em 10em auto;
  grid-template-areas: "image" "title" "content" "cta";
  text-align: center;
  /* block wrap sets the display type */
  /* /nested */
}
.block-type--hero_headline.block-view-mode--default .block-wrap > * {
  border: 1px solid blue;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .content {
  grid-area: content;
  display: flex;
  align-items: top;
  justify-content: center;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .content .text {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto", sans-serif;
  color: rgb(204.75, 195, 181.25);
  padding-top: 2em;
  padding-bottom: 2em;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .content .text strong,
.block-type--hero_headline.block-view-mode--default .block-wrap .content .text b {
  font-weight: 500;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .content .text .light {
  font-weight: 100;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .content .text p {
  margin-bottom: 0;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .image {
  border: 1px solid red;
  grid-area: image;
  display: flex;
  align-items: end;
  justify-content: center;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .image .media {
  width: 10em;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .image .media svg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .image .media svg path {
  fill: rgba(204.75, 195, 181.25, 0.5);
  opacity: 1;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .title {
  grid-area: title;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 5em;
  margin-right: 5em;
  border-bottom: 0.025em solid #d5b35b;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .title h2 {
  color: #635d59;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .title h3 {
  color: #d5b35b;
  font-style: normal;
  font-weight: 700;
  font-family: "Archivo", sans-serif;
  font-size: 3.06em;
  letter-spacing: -0.03em;
  margin-bottom: 0;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .title h3 strong,
.block-type--hero_headline.block-view-mode--default .block-wrap .title h3 b {
  font-weight: 900;
}
.block-type--hero_headline.block-view-mode--default .block-wrap .cta .field__items > .field__item a,
.block-type--hero_headline.block-view-mode--default .block-wrap .cta .field__items > .field__item a:visited {
  background-color: #d5b35b;
  color: #ffffff;
}

.block-type--hero_headline.block-view-mode--default.reverse-1 {
  background-color: #635d59;
  /* nested */
  /* /reverse nested */
}
.block-type--hero_headline.block-view-mode--default.reverse-1 .image svg path {
  fill: #ffffff;
  opacity: 1;
}
.block-type--hero_headline.block-view-mode--default.reverse-1 h2 {
  color: #ffffff;
}
.block-type--hero_headline.block-view-mode--default.reverse-1 .text * {
  color: #ffffff;
}